export const saveGame = () => {
    localStorage.setItem(`SAVE_V${version}`, JSON.stringify(state))
}

export const loadSavedGame = () => {
    Object.assign(state, JSON.parse(localStorage.getItem(`SAVE_V${version}`)))
}

export const deleteSave = () => {
    localStorage.removeItem(`SAVE_V${version}`)
}

export const hasSavedGame = () => localStorage.getItem(`SAVE_V${version}`) != null

export const newMessage = (message) => {
    playSound("FlashInfo");
    state.messages.push(message);
    document.querySelector(".button.map").classList.add('new')
}

export const nextEpoch = () => {
    state.currentEpoch++;
    if (state.currentEpoch >= 6) return;

    stopMusic();
    switch (state.currentEpoch) {
        case 2:
            newMessage({
                title: "Message prioritaire",
                text: "Ici le général Hammond. La situation est grave. Notre plan de contremesure nucléaire face à l'astéroïde ne peut pas être mis à exécution. Les missiles n'ont pas la vitesse et les capacités techniques d'atteindre la météorite suffisamment loin pour éviter le drame. La population l'ignore encore, et pourvu que ça dure."
            });
            // effet ?
            setTimeout(() => playMusic("2"), 6000);
            break;

        case 3:
            newMessage({
                title: "Flash spécial",
                text: "Une source interne au gouvernement nous informe que le plan de destruction de la météorite ne peut pas fonctionner. Le président refuse de répondre à nos questions et a été déplacé dans un endroit inconnu du public. Alors que les gens s'affolent et que les incidents se multiplient, certains refusent d'y croire, prétendant un complot ou une fake news comme en 2012."
            });
            setTimeout(() => playMusic("3"), 6000);
            break;

        case 4:
            newMessage({
                title: "La météorite visible du ciel",
                text: "Il n'y a désormais plus aucun doute possible alors que la météorite est désormais visible avec un téléscope classique. Les scientifiques disent qu'elle sera visible à l'oeil nu d'ici quelques jours. Les émeutes sont de plus en plus violentes, des groupes armés et des sectes mettent à sacs la plupart des grandes villes."
            });
            setTimeout(() => playMusic("4"), 6000);
            break;

        case 5:
            newMessage({
                title: "Que Dieu nous vienne en aide",
                text: "C'est la phrase prononcée en toutes les langues par les dirigeants du monde entier. Toutes les solutions envisagées ont échoué. Des rumeurs tournent autour d'un vaisseau spatial mais elles ne semblent pas crédibles. La société est à l'arrêt et on reporte de graves défaillances, notamment la centrale nucléaire qui émettrait d'importantes fumées radioactives. Mais plus personne n'y prête attention désormais. Ceci est notre dernière transmission, nous allons rejoindre nos familles. Adieu et bonne chance."
            });
            setTimeout(() => playMusic("5"), 6000);
            break;
    }
}